.Add-button {
    background-color: #4da1ff;
    color: white;
    width: 130px;
    height: 35px;
} 
.Icon {
    padding-right: 5px;
    position: relative;
    font-size: 12px !important;
}  
input {
    margin-left: 20px;
    padding: 8px 20px;
    font-size: 1em;
    border-radius: 3px;
    outline: none;
    border: 1px solid #ccc; 
    width: 70%;
}
li{
    background-color: #fff;
    line-height: 1.5rem;
    padding: 10px 20px;
    margin: 0;
    border-bottom: 1px solid #e0e0e0;
}
ul{
    margin: 0.5rem 0 1rem 0;
    list-style-type: none;
    overflow: hidden;
}
.Container{
    margin: 0.5rem 0 1rem 0;
    border: 1px solid #e0e0e0;
    border-radius: 2px;
    position: relative;
    overflow-x: hidden;
    background: white;
    width: 40%;
    display: block;
}
.Todo {
    padding: 20px;
    position: relative; 
    margin-bottom: 10px;  

}
.Footer{
    background-color: #F4FCE8;
    margin: 0 -20px -10px -20px;
    padding: 10px 20px; 
    top: 0;
    overflow: hidden;   
}
button {
    border-radius: 3px;
    border: none;
    color: white;
}
.All-button{
    background-color: #f19f0f;
}
.All-button, .Comp-button, .Act-button{
    width: 125px;
    height: 28px;
}
.Comp-button{
    background: #cd4436;
} .Act-button{
    background-color: #2ac56c;
}
.Footer-button{
    margin: 10px;
}
h1{
    text-align: center;
}
.li{
    width: 94%;
    color: #504f4f;
}
span{
    padding: 2px 4px !important;
    color: #fff;
    margin-left: 37px;
    font-weight: 900;
    font-size: 13px;
    border-radius: 6px;
    background-clip: padding-box;
    background: #6aaf2b;
}
Todo-list{
    overflow: hidden;
    position: relative
}
.Delete{
    float: right;
}
.deleteTodo {
display: inline;
}
a,button {
   cursor:pointer;
}
.Delete {
 display: none;
}
li:hover .Delete{
 display: inline !important;
}


